import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {v4 as uuidv4} from 'uuid';
import {withRouter, Link} from "react-router-dom";
import {DatePicker, Select, Button, Table, Pagination, Input, ConfigProvider, message, Icon, Modal, Radio, Popconfirm, Popover, Breadcrumb, Dropdown, Menu} from 'antd';
import MyUpload2 from "@/components/common/MyUpload2";
import {downloadFile, download} from "@/utils/download";
import EditableTableTwoItem from "./EditableTableTwoItem";
const InputGroup = Input.Group;
const {Option} = Select;
const {RangePicker} = DatePicker;
const { Search } = Input;

const dateFormat = 'YYYY-MM-DD';

const progressValue = (day1, day2, day3) =>{

	return [
		`1${day1}`,
		`2${day2}`,
		`3${day3}`
	]
}

const progressValueV4 = (day1, day2, day3) =>{

	if(day2===null || day2==="T+0"){
		return [
			`1${day1}`,
			`2${day3}`
		]
	}else{
		return [
			`1${day1}`,
			`2${day2}`,
			`3${day3}`
		]
	}

}


const qualityValue = (label, count) =>{
	return [
		`${label} ${count} ${label===''?'':''}`,
	]
}

const profitValue = (label, number) =>{
	return [
		`${label}     ${number===null?0:number}%`,
	]
}

const costValue = (label, number) =>{
	return [
		`${label} ${number} `,
	]
}


const safetyValue = (number1,number2) =>{
	return [
		` ${number1} `,
		` ${number2} `,
	]
}

const marketingValue = (flag1, flag2) =>{
	if(flag1===1&&flag2===1){
		return [
			`1`,
			`2`,
		]
	}
	if(flag1===1&&flag2===0){
		return [
			``,
		]
	}
	if(flag1===0&&flag2===1){
		return [
			``,
		]
	}
	if(flag1===0&&flag2===0){
		return []
	}

}





@withRouter
@observer
class TwoItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			dataList:[],
			bttiList:[],
			visible: false,
			stateValue: 0,
			id: "",
			buildTeamTwoMainObj:null

		}
	}

	componentDidMount() {
       this.getBuildTeamTwoMain();
       this.getBuildTeamTwoItemList();
	}

	getCostValue = () =>{
		const {  buildTeamTwoMainObj } = this.store;
		const { costTargetOne, costTargetTwo, costTargetThree, costTargetFour, costTargetThree2, costTargetFour2} = mobx.toJS(buildTeamTwoMainObj);
		const dataInfo =[
			{"show":costTargetOne, "info":": "},
			{"show":costTargetTwo, "info":": 2002000; 2003000"},
			{"show":costTargetThree, "info":": *2  2000"},
			{"show":costTargetThree2, "info":": 2  1500"},
			{"show":costTargetFour, "info":": *5  10000"},
			{"show":costTargetFour2, "info":":5  5000"},
		]
		let result = dataInfo.filter((item)=>{
			return item.show===1
		})
		result = result.map((item)=>{
			return item.info
		})
		return result
	}


	getBuildTeamTwoMain=()=>{
		const { taskDetail, buildTeamTwoMainObj, buildTeamState } = this.store;
		this.store.getBuildTeamTwoMain({"taskId":taskDetail.id}).then((res)=>{
            this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res);
            let targetDay1 = res.progressTargetTimeSure===1? moment(res.progressTargetBaseTime).format(dateFormat):"T";
			let targetDay2 = targetDay1==="T"?"T+"+res.progressTargetNostandardDay+"":moment(targetDay1).add(res.progressTargetNostandardDay,"days").format(dateFormat);
			let targetDay3 = targetDay1==="T"?"T+"+res.progressTargetOverDay+"":moment(targetDay1).add(res.progressTargetOverDay,"days").format(dateFormat)

			let promiseDay1 = res.progressPromiseTimeSure===1? moment(res.progressPromiseBaseTime).format(dateFormat):"T";
			let promiseDay2 = promiseDay1==="T"?"T+"+res.progressPromiseNostandardDay+"":moment(promiseDay1).add(res.progressPromiseNostandardDay,"days").format(dateFormat);
			let promiseDay3 = promiseDay1==="T"?"T+"+res.progressPromiseOverDay+"":moment(promiseDay1).add(res.progressPromiseOverDay,"days").format(dateFormat)

			let profitTargetValue=res.profitTarget===0?"":profitValue("",res.profitTargetRate)

			let dl = [
				{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
				{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"", "targetValue":qualityValue("",res.qualityTargetCheckCount), "promiseValue": qualityValue("",res.qualityPromiseAccidentCount)},
				{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"", "targetValue":profitTargetValue, "promiseValue": ""},
				{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"", "targetValue":this.getCostValue()},
				{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"", "targetValue":marketingValue(res.markingTargetSatisfied,res.markingTargetStandard), "promiseValue": ""},
				{"keyIndex":6,  "id":"!"+uuidv4(), "orders":6, "item":"", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
			]

			if(buildTeamState.state2===2){
				if(res.costTargetFour2===0&&res.costTargetThree2===0&&res.costTargetOne===0&&res.costTargetTwo===0&&res.costTargetThree===0&&res.costTargetFour===0&&res.markingTargetSatisfied===0&&res.markingTargetStandard===0){
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"", "targetValue":qualityValue("",res.qualityTargetCheckCount), "promiseValue": qualityValue("",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}else if(res.costTargetOne===0&&res.costTargetTwo===0&&res.costTargetFour2===0&&res.costTargetThree===0&&res.costTargetThree2===0&&res.costTargetFour===0){
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"", "targetValue":qualityValue("",res.qualityTargetCheckCount), "promiseValue": qualityValue("",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"", "targetValue":marketingValue(res.markingTargetSatisfied,res.markingTargetStandard), "promiseValue": ""},
						{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}else if(res.markingTargetSatisfied===0&&res.markingTargetStandard===0){
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"", "targetValue":qualityValue("",res.qualityTargetCheckCount), "promiseValue": qualityValue("",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"", "targetValue":this.getCostValue()},
						{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}else{
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"", "targetValue":qualityValue("",res.qualityTargetCheckCount), "promiseValue": qualityValue("",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"", "targetValue":this.getCostValue()},
						{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"", "targetValue":marketingValue(res.markingTargetSatisfied,res.markingTargetStandard), "promiseValue": ""},
						{"keyIndex":6,  "id":"!"+uuidv4(), "orders":5, "item":"", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}
			}

            this.setState({dataList:dl, buildTeamTwoMainObj:res})
		})
	}

	getBuildTeamTwoItemList=()=>{
		const { taskDetail } = this.store;
		this.store.getBuildTeamTwoItemList({"taskId": taskDetail.id}).then((res)=>{
			this.setState({bttiList:res})
		})
	}

	saveBuildTeamTwoItem=(params)=>{
		this.store.saveBuildTeamTwoItem(params).then((res)=>{
			this.getBuildTeamTwoItemList();
		})
	}


	saveContents=(dataSource)=>{
		console.log("dataSource=>", dataSource);
		this.setState({bttiList:dataSource})
	}



	saveItem=(row)=>{
		const {taskDetail} = this.store;
		const {dataList, bttiList} = this.state;
		let newItem={
			"buildTeamTwo":taskDetail.id,
			"orders":dataList.length+bttiList.length+1,
			"targetValue":row.targetValue,
			"promiseValue":row.promiseValue,
			"item":row.item,
		}
		if(row.id!==null || row.id!==undefined){
			newItem['id']=row.id
			newItem['orders']= row.orders
		}
		this.saveBuildTeamTwoItem({"btti":newItem})
	}

	convertToTableData=()=>{
		const {dataList, bttiList} = this.state;
		return dataList.map((item,index)=>{
					return Object.assign(item, {"dataType":"main"})
				}).concat(
					bttiList.map((item,index)=>{
						return Object.assign(item, {"dataType":"item", "keyIndex":dataList.length+1+index})
					})
				)
	}

	overCurrentStage=()=>{
		const cpt = this.checkProfitTarget();
		if(!cpt.result){
			message.warn(cpt.info)
			return
		}

		// 
		let buildTeamTwoMainObj = mobx.toJS(this.store.buildTeamTwoMainObj);
		if(buildTeamTwoMainObj.progressTargetTimeSure===1&&(buildTeamTwoMainObj.progressTargetBaseTime==null||buildTeamTwoMainObj.progressTargetBaseTime==0)){
		  message.warn("");
		  return false;
		}
		if(buildTeamTwoMainObj.progressPromiseTimeSure===1&&(buildTeamTwoMainObj.progressPromiseBaseTime==null||buildTeamTwoMainObj.progressPromiseBaseTime==0)){
			message.warn("");
			return false;
		}

		this.props.overCurrentStageTask();
		setTimeout(()=>{
			this.getBuildTeamTwoMain();
			this.getBuildTeamTwoItemList();
		},500)
	}

	doneAndNext=()=>{
		const { currentStep } = this.props;
		const cpt = this.checkProfitTarget();
		if(!cpt.result){
			message.warn(cpt.info)
			return
		}

		// 
		const buildTeamTwoMainObj = mobx.toJS(this.store.buildTeamTwoMainObj);
		if(buildTeamTwoMainObj.progressTargetTimeSure===1&&(buildTeamTwoMainObj.progressTargetBaseTime==null||buildTeamTwoMainObj.progressTargetBaseTime==0)){
			message.warn("");
			return false;
		}
		if(buildTeamTwoMainObj.progressPromiseTimeSure===1&&(buildTeamTwoMainObj.progressPromiseTimeSure==null||buildTeamTwoMainObj.progressPromiseTimeSure==0)){
			message.warn("");
			return false;
		}

		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep+1)
	}

	reStartBuildTeam=()=>{
		this.props.reStartBuildTeam();
		setTimeout(()=>{
			this.getBuildTeamTwoMain();
		},500)

	}

	checkProfitTarget=()=>{
		let resultObj = {"info":"",result:false};
		const { buildTeamTwoMainObj} = this.store;
		if(buildTeamTwoMainObj.profitTarget===1&&buildTeamTwoMainObj.profitTargetRate!==null&&buildTeamTwoMainObj.profitTargetRate>0){
			resultObj.result = true
		}
		if(buildTeamTwoMainObj.profitTarget===0){
			resultObj.result = true
		}
		return resultObj

	}

	render() {
		const {buildTeamState} = this.store;
		const {dataList, bttiList} = this.state;
		const { currentStep, switchSteps } = this.props;
		return (
			<div className="mt30">
				<div className="mb10">
					<Button
						ghost
						type="primary"
						icon="poweroff"
						onClick={this.reStartBuildTeam}
						style={buildTeamState.state2===2?{display:'',cursor:'pointer'}:{display:'none'}}
					>
						
					</Button>
				</div>
				<EditableTableTwoItem
					id={'id'}
					store={this.store}
					dataSource={this.convertToTableData()}
					saveData={this.saveContents}
					saveItem={this.saveItem}
					disabled={buildTeamState.state2===2?true:false}
					pid={''}
				/>
				<div  className="mt30 mb30" style={buildTeamState.state2!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button
						ghost
						type="primary"
						onClick={this.overCurrentStage}> </Button><Button onClick={this.doneAndNext} style={{marginLeft:'20px'}} type='primary'> </Button>
				</div>
				<div  className="mt30 mb30" style={buildTeamState.state2===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep+1)}} style={{marginLeft:'20px'}} type='primary'></Button>
				</div>
			</div>
		)
	}
}


export default TwoItem;



