import React from 'react';
import {inject, observer} from 'mobx-react';
import {Button} from 'antd';
import { Table } from 'antd';
import Document from "@/components/supplier/Document";
import moment from "moment/moment";
import Zmage from 'react-zmage';
import SendEmail from './SendEmail';

const dateFormat = 'YYYY-MM-DD';

@inject('DeliveryStore')
@observer
class View extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeliveryStore;
		this.state = {
            makePlotInfo: {},  // 
            buildTeamTwoMain: {}, //
            makePlotCost: {},
            makePlotPeople: [],
            makePlotMachine: [],
            makePlotMaterial: [],
            makePlotQuality: [],
            makePlotRisk: [],
            makePlotSafe: [],
            obj:{}
        }
	}

    componentDidMount() {
        this.getMakePlot();
    }

    getMakePlot = () => {
        const projectId =this.store.projectId;
        this.store.getMakePlot({'projectId':projectId}).then((mres)=>{
            if(mres) {
                let res = JSON.parse(JSON.stringify(mres));
                this.setState({
                    obj: mres,
                    makePlotInfo: res.makePlotInfo,
                    buildTeamTwoMain: res.buildTeamTwoMain,
                    makePlotCost: res.makePlotCost,
                    makePlotPeople: Array.isArray(res.makePlotPeople) ? res.makePlotPeople.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotMachine: Array.isArray(res.makePlotMachine) ? res.makePlotMachine.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotMaterial: Array.isArray(res.makePlotMaterial) ? res.makePlotMaterial.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotQuality: Array.isArray(res.makePlotQuality) ? res.makePlotQuality.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotRisk: Array.isArray(res.makePlotRisk) ? res.makePlotRisk.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotSafe: Array.isArray(res.makePlotSafe) ? res.makePlotSafe.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                });
            }
        });
    }

	render() {
        const {
            makePlotInfo,
            buildTeamTwoMain,
            makePlotCost,
            makePlotPeople,
            makePlotMachine,
            makePlotMaterial,
            makePlotQuality,
            makePlotRisk,
            makePlotSafe,
            obj
        } = this.state;
        const columns1 = [
            {
                title: '',
                dataIndex: 'key',
                key: 'key',
                align: 'center'
            },
            {
                title: '',
                dataIndex: 'age',
                key: 'age',
                align: 'center'
            },
            {
                title: '',
                dataIndex: 'address',
                key: 'address',
                align: 'left',
                render: (text, record)=> {
                    if (record.key==1) {
                        const progressValueV4 = (day1, day2, day3) =>{
                            return [
                                <p>1{day1}</p>,
                                <p>2{day2}</p>,
                                <p>3{day3}</p>
                            ]
                        }
                        let targetDay1 = buildTeamTwoMain.progressTargetTimeSure===1? moment(buildTeamTwoMain.progressTargetBaseTime).format(dateFormat):"T";
                        let targetDay2 = targetDay1==="T"?"T+"+buildTeamTwoMain.progressTargetNostandardDay+"":moment(targetDay1).add(buildTeamTwoMain.progressTargetNostandardDay,"days").format(dateFormat);
                        let targetDay3 = targetDay1==="T"?"T+"+buildTeamTwoMain.progressTargetOverDay+"":moment(targetDay1).add(buildTeamTwoMain.progressTargetOverDay,"days").format(dateFormat)
                        return (<div>
                            {progressValueV4(targetDay1,targetDay2,targetDay3)}
                        </div>)
                    } else if (record.key==2) {
                        return (<div>
                            <p>  {buildTeamTwoMain.qualityTargetCheckCount}  </p>
                        </div>)
                    } else if (record.key==3) {
                        return (<div>
                            <p>{buildTeamTwoMain.profitTargetRate}%</p>
                        </div>)
                    } else if (record.key==4) {
                        return (<div>
                            {buildTeamTwoMain.markingTargetStandard==1? <p></p>:null}
                            {buildTeamTwoMain.markingTargetSatisfied==1? <p></p>:null}
                        </div>)
                    } else if (record.key==5) {
                        return (<div>
                            <p>  {buildTeamTwoMain.safeTargetAccidentCount}  </p>
                            <p>  {buildTeamTwoMain.safeTargetTreacherousCount}  </p>
                        </div>)
                    }
                }
            },
        ];

        const dataSource1 = [
            {
                key: '1',
                age: '',
                address: '1',
            },
            {
                key: '2',
                age: '',
                address: '1',
            },
            {
                key: '3',
                age: '',
                address: '1',
            },
            {
                key: '4',
                age: '',
                address: '1',
            },
            {
                key: '5',
                age: '',
                address: '1',
            }
        ];

        const columns2 = [
            {
                title: '',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'count',
                key: 'count',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'enterTime',
                key: 'enterTime',
                align: 'center',
                className:'vam',
                render: (text, record)=> {
                    return text==0||text==null?"":moment(text).format('YYYY-MM-DD')
                }
            },
            {
                title: "",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];

        const columns3 = [
            {
                title: '',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'specs',
                key: 'specs',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'specs',
                key: 'specs',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];

        const columns4 = [
            {
                title: '',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'enterBeginTime',
                key: 'enterBeginTime',
                align: 'center',
                className:'vam',
                render: (text, record)=> {
                    return text==0||text==null?"":moment(record["enterBeginTime"]).format('YYYY-MM-DD') +""+moment(record["enterEndTime"]).format('YYYY-MM-DD')
                }
            },
            {
                title: "",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];
        const columns5 = [
            {
                title: '',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'buildTeamName',
                key: 'buildTeamName',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];
        const columns6 = [
            {
                title: '',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'buildTeamName',
                key: 'buildTeamName',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }

            }];
        const columns7= [
            {
                title: '',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "",
                dataIndex: 'type',
                key: 'type',
                align: 'center',
                className:'vam',
                editable: true,
                render: (text, record) => {
                    return (<div
                        style={{verticalAlign:"middle" }}>
                        {text}
                    </div>);
                }
            },
            {
                title: "",
                dataIndex: 'describes',
                key: 'describes',
                align: 'left',
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            },
            {
                title: "",
                dataIndex: 'programme',
                key: 'programme',
                align: 'left',
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%',  lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];

        const state1 = {0: "", 1: "", 2: ""};
        const state2 = {0: "", 1: "", 2: ""};

        let isDocument = makePlotInfo.document==null || (typeof makePlotInfo.document == "object"&&Object.keys(makePlotInfo.document).length==0)?false:true;  // 


        return (
			<div style={{padding: "0 15px"}}>
                <Button
                    ghost
                    type="primary"
                    icon="poweroff"
                    onClick={this.props.reStartHandover}
                >
                    
                </Button>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
                <table className="suppliertable" style={{width: '100%'}}>
                    <tr>
                        <td></td>
                        <td>
                            {makePlotInfo.name}
                        </td>
                        <td></td>
                        <td>
                            {makePlotInfo.address}
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                            {makePlotInfo.partyUnits}
                        </td>
                        <td></td>
                        <td>
                            {makePlotInfo.buildUnits}
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                            {makePlotInfo.supplier==null?"":makePlotInfo.supplier.map((item)=>{
                                return item.name
                            }).join("")}
                        </td>
                        <td></td>
                        <td>
                            {makePlotInfo.projectConstructionType}
                        </td>
                    </tr>
                    <tr>
                        <td>m2</td>
                        <td>
                            {makePlotInfo.buildArea}
                        </td>
                        <td></td>
                        <td>
                            {makePlotInfo.roomComposition}
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                            {makePlotInfo.enterCondition}
                        </td>
                        <td></td>
                        <td>
                            {makePlotInfo.productType}
                        </td>
                    </tr>
                </table>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
                <Table
                    className="digital_table agenda_nohover"
                    pagination={false}
                    bordered
                    dataSource={dataSource1}
                    columns={columns1} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
                <div className="w" style={{textAlign: "center", padding: "15px 0"}}>
                    <Zmage
                        style={{width: "80%",
                            height: "350px"}}
                        src={makePlotInfo.orgImageUrl} />
                </div>
                <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                     dangerouslySetInnerHTML={{__html: makePlotInfo.organizationRemark}}></div>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
                <table className="makeplotTable">
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                    <tr>
                        <td>1</td>
                        <td>
                            
                        </td>
                        <td>{makePlotInfo.partyUnit}</td>
                        <td>
                            {state1[makePlotCost.firstContractState]}
                        </td>
                        <td>{makePlotCost.firstContractValue}</td>
                    </tr>
                    {makePlotCost&&makePlotCost.confirmSupplierTwoItems&&makePlotCost.confirmSupplierTwoItems.map((item, index)=>{
                        return <tr key={item.id}>
                            <td>{index+2}</td>
                            <td>
                                
                            </td>
                            <td>{item.supplierName}</td>
                            <td>
                                {state1[item.isActive]}
                            </td>
                            <td>{item.contractMoney}</td>
                        </tr>
                    })}
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+2)}</td>
                        <td>
                            
                        </td>
                        <td></td>
                        <td>
                            {state2[makePlotCost.civilizedConstructionState]}
                        </td>
                        <td>
                            {makePlotCost.civilizedConstructionValue}</td>
                    </tr>
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+3)}</td>
                        <td>
                            
                        </td>
                        <td></td>
                        <td>
                            {state2[makePlotCost.hospitalityState]}
                        </td>
                        <td>{makePlotCost.hospitalityValue}</td>
                    </tr>
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+4)}</td>
                        <td>
                            
                        </td>
                        <td></td>
                        <td>
                            {state2[makePlotCost.additionalRiskState]}
                        </td>
                        <td>
                            {makePlotCost.additionalRiskValue}
                        </td>
                    </tr>
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+5)}</td>
                        <td>
                            
                        </td>
                        <td></td>
                        <td>
                        </td>
                        <td>{makePlotCost.yields}</td>
                    </tr>
                </table>
                <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                     dangerouslySetInnerHTML={{__html: makePlotCost.remark}}></div>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}> </h1>
                <p className="mt15 mb15">1</p>
                <table className="suppliertable" style={{width: '100%'}}>
                    <tr>
                        <td></td>
                        <td>
                            {makePlotCost.firstPartyTimeRange}
                        </td>
                        <td></td>
                        <td>
                            {makePlotCost.supplierTimeRange}
                        </td>
                    </tr>
                </table>
               <p className="mt15 mb15">2</p>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotPeople}
                    columns={columns2} />
                <p className="mt15 mb15">3</p>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotMachine}
                    columns={columns3} />
                <p className="mt15 mb15">4</p>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotMaterial}
                    columns={columns4} />
                <p className="mt15 mb15">5</p>
                <div className="w" style={{textAlign: "center", padding: "15px 0"}}>
                    <Zmage
                        style={{
                            maxWidth: "75%",
                            height: "auto"
                        }}
                        src={makePlotInfo.planImageUrl} />
                </div>
                <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                     dangerouslySetInnerHTML={{__html: makePlotInfo.planImageRemark}}></div>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotQuality}
                    columns={columns5} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotSafe}
                    columns={columns6} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotRisk}
                    columns={columns7} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}></h1>
               {isDocument?<Document
                    data={makePlotInfo.document}
                    store={this.store}
                />:null}
                <SendEmail
                    obj={obj}
                    store={this.store}
                />
			</div>
		)
	}
}


export default View;



